#tv {
  /* position: relative; */
  width: 350px;
  height: 550px;
  background: white;
  border-radius: 0% 0% 0% 0% / 0% 0% 0% 0% ;
  color: rgb(192, 98, 98);
  box-shadow: 30px 25px rgba(0,0,0,.15);
  transition: all .4s ease;
}
#tv:hover {
  border-radius: 0% 0% 50% 50% / 0% 0% 5% 5% ;
  box-shadow: 10px 10px rgba(0,0,0,.25);
}
#complete-step {
  width: 9em;
  height: 3em;
  border-radius: 30em;
  font-size: 18px;
  font-family: inherit;
  border: none;
  position: relative;
  overflow: hidden;
  z-index: 1;


}

#complete-step::before {
 content: '';
 width: 0;
 height: 3em;
 border-radius: 30em;
 position: absolute;
 top: 0;
 left: 0;
 background-image: linear-gradient(to right, #eea849 0%, #f46b45 100%);
 transition: .5s ease;
 display: block;
 z-index: -1;
}

#complete-step:hover::before {
  width: 9em;
}

#complete-step.step-complete{
  background-color: rgb(142, 108, 206);
  color: white;
}

